import React, { useState} from 'react';
import { Link, useNavigate  } from 'react-router-dom';
import { Input, Button, EmailInput } from '@ya.praktikum/react-developer-burger-ui-components'
import styles from './pages.module.css';
import { useDispatch, useSelector } from '../types/types';
import { signIn } from '../services/login/login-action';
import { TLoginType } from '../types/types';

export function LoginPage() {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const apiData = useSelector(store => store.login);
  const [showPassword, setShowPassword] = useState<boolean>(false);
  const [user, setUser] = useState<TLoginType>({email: '', password: ''})

  const onIconClick = () => {
    setShowPassword(!showPassword);
  };

  const onSubmit = (e: React.FormEvent<HTMLFormElement>) => {
      e.preventDefault();
      dispatch(signIn(user));
      if (apiData.loginSuccessful) {navigate(-1)};
  };

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setUser({ ...user, [e.target.name]: e.target.value });
  };

  const linksStyle: string = `${styles.pagesLinks} text text_type_main-default`;

  return (
      <div className={styles.mainDiv}>
      <form onSubmit={onSubmit} className={styles.loginPage}>
          <p className='text text_type_main-medium'></p>
          <EmailInput
            onChange={e => handleChange(e)}
            value={user.email}
            name={'email'}
            isIcon={false}
            extraClass="ml-1"/>
          <Input
            type={showPassword ? 'text' : 'password'}
            placeholder={''}
            onChange={e => handleChange(e)}
            icon={'ShowIcon'}
            value={user.password}
            name={'password'}
            error={false}
            onIconClick={onIconClick}
            errorText={''}
            extraClass="ml-1"/>
          {apiData.errMsg && <p className={`${styles.errMsg} text text_type_main-default`}>{apiData.errMsg}</p>}
          <Button htmlType="submit" type="primary" size="large"></Button>
          <div className={styles.toolTips}>
              <p className="text text_type_main-default text_color_inactive">   ?</p>
              <Link to={'/register'} className={linksStyle}></Link>
          </div>
          <div className={styles.toolTips}>
              <p className="text text_type_main-default text_color_inactive"> ?</p>
              <Link to={'/forgot-password'} className={linksStyle}> </Link>
          </div>
      </form>
      </div>
    );
}